.closeBtn {
  position: absolute;
  right: 6px;
  top: 8px;

  width: 26px;
  height: 26px;
  
  background: url('/src/assets/ui/btn_close.png') no-repeat;
  background-size: contain;
  border: none;

  -webkit-transition: all 0.3s;

  transition: all 0.3s;
}

/* dom element test - 9 slice */

.pixelBox {
  position: relative;

  width: 39px;
  height: 39px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;

  text-align: center;
  /* transform: translate(400%, 10%); */
  -webkit-transition: all 0.6s ease-in;
  transition: all 0.6s ease-in;
}

.pixel {
  width: 13px;
  height: 13px;

  min-width: 8px;
  min-height: 8px;

  float: left;
  background-size: cover;
}

.pixel:nth-child(1) {
  background:url('/src/assets/ui/modal/nineBox_01.png') no-repeat;
}

.pixel:nth-child(2) {
  background:url('/src/assets/ui/modal/nineBox_02.png') repeat-x;
}

.pixel:nth-child(3) {
  background:url('/src/assets/ui/modal/nineBox_03.png') no-repeat;
}

.pixel:nth-child(4) {
  background:url('/src/assets/ui/modal/nineBox_04.png') repeat-y;
}

.pixel:nth-child(5) {
  background:url('/src/assets/ui/modal/nineBox_05.png') repeat;
}

.pixel:nth-child(6) {
  background:url('/src/assets/ui/modal/nineBox_06.png') repeat-y;
}

.pixel:nth-child(7) {
  background:url('/src/assets/ui/modal/nineBox_07.png') no-repeat;
}

.pixel:nth-child(8) {
  background:url('/src/assets/ui/modal/nineBox_08.png') repeat-x;
}

.pixel:nth-child(9) {
  background:url('/src/assets/ui/modal/nineBox_09.png') no-repeat;
}

#Game, canvas {
  position: relative;

  display: block;
  margin: 0 auto;
}

.project_header {
  text-align: center;
}

.project_header h1 {
  margin: 80px 0;


  color: #ffffff;
  font-size: 40px;
  line-height: 1;
}

.uiContainer {
  position: absolute;
  z-index: 9999;
  
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
  width: 980px;
  height: 500px;
  margin: 0 auto;

  text-align: center;
  overflow: hidden;

  /* 클릭이벤트 막기. */
  /* pointer-events: none; */
}

/* 클릭이벤트 열기. */

.uiContainer * {
  pointer-events: auto;
}

.modal {
  top: 20px;
  right: 20px;
}

.button {
  width: 130px;
  height: 33px;
  padding: 8px 0;
  color: #fff;
  border: none;
  background: url(src/assets/ui/button1.png) no-repeat;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}

.button._small {
  width: 79px;
  padding: 8px 0;
  background: url(src/assets/ui/button_s1.png) no-repeat;
  background-size: contain;
}

/* 사용 미정 */

.button._small.submit {
  background: url(src/assets/ui/button_s2.png) no-repeat;
}

.button.submit {
  background: url(src/assets/ui/button2.png) no-repeat;
}

.title {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;

  text-align: center;
  font-size: 26px;
  color: #ffd800;
  text-align: center;
}

.stageTitle {
  display: inline-block;
  margin:15% auto;
  
  text-align: center;
  font-size: 40px;
  line-height: 50px;
  color: #fff;
  border-bottom: 3px solid #fff;

  -webkit-transform: scaleX(0);

          transform: scaleX(0);
  opacity:0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.stageTitle.animate {
  opacity:1;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;

}

.contents {
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;

  margin: 0 auto;
  font-size: 15px;
  line-height: 1.5;
  color: #fff;
}

